*{margin:0;padding:0;box-sizing:border-box}html{scroll-behavior:smooth}.loader{text-align:center;display:flex;justify-self:center;align-self:center;display:block;margin:20rem auto}body{margin:0 0 3rem 0;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:1.2rem}.lightTheme{background-color:#fdf5ff;color:#747474}.darkTheme{background-color:#311042;color:#f4e2fd}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}h1,h2,h3,h4{color:#4d4d4d;font-weight:700}ul{list-style:none}a{text-decoration:none}img{max-width:100%;height:auto}.section{padding:2rem 0 3rem}.sectionTitle{font-size:1.8rem}.sectionSubtitle{display:block;font-size:1.1rem;margin-bottom:2rem}.sectionTitle,.sectionSubtitle{text-align:center}.sectionTitleDark,.sectionSubtitleDark{color:#fdf5ff}.container{max-width:95%;margin:0 auto}.grid{display:grid;gap:1.5rem}.scrollup{position:fixed;right:1rem;bottom:-20rem;background-color:#9932cc;color:#fdf5ff;opacity:0.8;padding:0 0.3rem;border-radius:0.4rem;z-index:10;transition:0.5s}.scrollup:hover{background-color:#c042ff}.scrollup.active{bottom:4rem;transition:0.5s}::-webkit-scrollbar{width:0.6rem;background-color:#f4e2fd;border-radius:0.5rem}::-webkit-scrollbar-thumb{background-color:#9932cc;border-radius:0.5rem}::-webkit-scrollbar-thumb:hover{background-color:#311042}@media screen and (max-width: 350px){.container{max-width:90%}}@media screen and (min-width: 768px){.container{max-width:80%}body{margin:0}.section{padding:6rem 0 2rem}.sectionSubtitle{margin-bottom:4rem}}@media screen and (min-width: 1024px){.container{max-width:70%}}.testimonialData{display:flex;flex-direction:column;justify-content:space-evenly !important;margin-bottom:1rem}.testimonialHeader{display:flex;justify-content:space-between;align-items:center;margin-bottom:.75rem}.testImg{width:60px;height:60px;border-radius:50%;margin-right:.75rem;object-fit:cover}.nameSection{margin-bottom:1rem}.testimonialName{font-size:1.3rem;font-weight:500}.testimonialNameDark,.testimonialBusinessDark{color:#fdf5ff}.testimonialBusiness{font-size:1.1rem;color:#999}.testimonialDesc{margin-bottom:2.5rem !important}.starIcon{color:#9932cc}.testimonials{overflow:hidden}.testimonials .swiper-container{width:800px;margin-left:auto;margin-right:auto}.testimonials .swiper-container .swiper-slide{font-size:18px;margin:1rem 0 2rem;padding:1rem;border-radius:10px;box-shadow:0 4px 4px rgba(0,0,0,0.15);height:270px;display:-webkit-box;display:-ms-flexbox;display:-webkit-flex;display:flex;-webkit-box-pack:center;-ms-flex-pack:center}@media screen and (min-width: 1250px){.testimonials .swiper-container{width:100%;padding-top:50px;padding-bottom:50px}}
