*{margin:0;padding:0;box-sizing:border-box}html{scroll-behavior:smooth}.loader{text-align:center;display:flex;justify-self:center;align-self:center;display:block;margin:20rem auto}body{margin:0 0 3rem 0;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:1.2rem}.lightTheme{background-color:#fdf5ff;color:#747474}.darkTheme{background-color:#311042;color:#f4e2fd}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}h1,h2,h3,h4{color:#4d4d4d;font-weight:700}ul{list-style:none}a{text-decoration:none}img{max-width:100%;height:auto}.section{padding:2rem 0 3rem}.sectionTitle{font-size:1.8rem}.sectionSubtitle{display:block;font-size:1.1rem;margin-bottom:2rem}.sectionTitle,.sectionSubtitle{text-align:center}.sectionTitleDark,.sectionSubtitleDark{color:#fdf5ff}.container{max-width:95%;margin:0 auto}.grid{display:grid;gap:1.5rem}.scrollup{position:fixed;right:1rem;bottom:-20rem;background-color:#9932cc;color:#fdf5ff;opacity:0.8;padding:0 0.3rem;border-radius:0.4rem;z-index:10;transition:0.5s}.scrollup:hover{background-color:#c042ff}.scrollup.active{bottom:4rem;transition:0.5s}::-webkit-scrollbar{width:0.6rem;background-color:#f4e2fd;border-radius:0.5rem}::-webkit-scrollbar-thumb{background-color:#9932cc;border-radius:0.5rem}::-webkit-scrollbar-thumb:hover{background-color:#311042}@media screen and (max-width: 350px){.container{max-width:90%}}@media screen and (min-width: 768px){.container{max-width:80%}body{margin:0}.section{padding:6rem 0 2rem}.sectionSubtitle{margin-bottom:4rem}}@media screen and (min-width: 1024px){.container{max-width:70%}}.quaTab{display:flex;align-items:center;justify-content:space-evenly;margin-bottom:2rem}.quaButton{display:flex;align-items:center;font-size:1.1rem;font-weight:bold;cursor:pointer}.quaButton:hover{color:#9932cc}.quaButton.active{color:#9932cc}.quaButton .quaIcon{margin-right:.25rem}.quaData{display:grid;grid-template-columns:1fr max-content 1fr;column-gap:1.5rem}.quaData .quaTitle{font-size:1.2rem;font-weight:500;margin-bottom:0.2rem}.quaData .quaSubtitle{display:inline-block;font-weight:1.1rem;margin-bottom:1rem}.quaData .quaCalendar{font-size:1rem;color:#999;display:flex;align-items:center}.quaData .quaCalendar .calIcon{margin-right:0.2rem}.quaData .quaRounder{display:inline-block;width:13px;height:13px;background-color:#9932cc;border-radius:50%}.quaData .quaLine{display:block;width:1px;height:100%;background-color:#9932cc;transform:translate(6px, -7px)}.quaClose{height:0;overflow:hidden;transition:1s}.quaOpen{height:max-content;margin-bottom:1rem;transition:1s}.quaTitleDark{color:#c042ff}@media screen and (min-width: 560px){.quaSections{display:grid;grid-template-columns:0.7fr;justify-content:center}}@media screen and (min-width: 768px){.quaTab{justify-content:center}.quaButton{margin:0 2.5rem}.quaSections{grid-template-columns:0.5fr}}
