*{margin:0;padding:0;box-sizing:border-box}html{scroll-behavior:smooth}.loader{text-align:center;display:flex;justify-self:center;align-self:center;display:block;margin:20rem auto}body{margin:0 0 3rem 0;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:1.2rem}.lightTheme{background-color:#fdf5ff;color:#747474}.darkTheme{background-color:#311042;color:#f4e2fd}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}h1,h2,h3,h4{color:#4d4d4d;font-weight:700}ul{list-style:none}a{text-decoration:none}img{max-width:100%;height:auto}.section{padding:2rem 0 3rem}.sectionTitle{font-size:1.8rem}.sectionSubtitle{display:block;font-size:1.1rem;margin-bottom:2rem}.sectionTitle,.sectionSubtitle{text-align:center}.sectionTitleDark,.sectionSubtitleDark{color:#fdf5ff}.container{max-width:95%;margin:0 auto}.grid{display:grid;gap:1.5rem}.scrollup{position:fixed;right:1rem;bottom:-20rem;background-color:#9932cc;color:#fdf5ff;opacity:0.8;padding:0 0.3rem;border-radius:0.4rem;z-index:10;transition:0.5s}.scrollup:hover{background-color:#c042ff}.scrollup.active{bottom:4rem;transition:0.5s}::-webkit-scrollbar{width:0.6rem;background-color:#f4e2fd;border-radius:0.5rem}::-webkit-scrollbar-thumb{background-color:#9932cc;border-radius:0.5rem}::-webkit-scrollbar-thumb:hover{background-color:#311042}@media screen and (max-width: 350px){.container{max-width:90%}}@media screen and (min-width: 768px){.container{max-width:80%}body{margin:0}.section{padding:6rem 0 2rem}.sectionSubtitle{margin-bottom:4rem}}@media screen and (min-width: 1024px){.container{max-width:70%}}.portfolioContainer{overflow:initial}.portfolioContent{padding:0 1.5rem}.portfolioImg{width:300px;height:160px;border-radius:0.7rem;justify-self:center;object-fit:cover}.portfolioData{display:flex;justify-content:space-between;flex-direction:column}.portfolioTitle{font-size:1.3rem;margin:5px 0;color:#4d4d4d}.portfolioTitleDark{color:#fdf5ff}.portfolioSubtitle{font-size:1.1rem;font-weight:500;margin-bottom:.5rem}.portfolioDesc{margin-bottom:.75rem;max-width:300px;font-size:0.9rem}.portfolioButton{display:flex;align-items:center;justify-content:center;padding:8px 10px;max-width:110px;background-color:#9932cc;color:white;border-radius:8px;box-shadow:0 4px 4px rgba(0,0,0,0.15);transition:all 1s ease}.portfolioButton .btnIcon{margin-left:4px}.portfolioButton:hover{background-color:#5a0086;box-shadow:0 5px 8px 4px rgba(0,0,0,0.2)}.portfolioButton:hover>.btnIcon{transform:translateX(0.5rem)}.portfolioContainer>.swiper-container{width:100%;padding-top:50px;padding-bottom:50px}.portfolioContainer .swiper-slide{background-position:center;background-size:cover;width:300px;height:450px !important}.swiper-pagination-bullet-active{background-color:#9932cc !important}.portfolioContainer>.swiper-slide .portfolioContent{display:block;width:100%}@media screen and (max-width: 650px){.portfolioContent{padding:0.75rem}.portfolioContainer .swiper-slide{width:270px}}@media screen and (min-width: 1250px){.portfolioContainer>.swiper-container{width:100%;padding-top:50px;padding-bottom:50px}}
