*{margin:0;padding:0;box-sizing:border-box}html{scroll-behavior:smooth}.loader{text-align:center;display:flex;justify-self:center;align-self:center;display:block;margin:20rem auto}body{margin:0 0 3rem 0;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:1.2rem}.lightTheme{background-color:#fdf5ff;color:#747474}.darkTheme{background-color:#311042;color:#f4e2fd}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}h1,h2,h3,h4{color:#4d4d4d;font-weight:700}ul{list-style:none}a{text-decoration:none}img{max-width:100%;height:auto}.section{padding:2rem 0 3rem}.sectionTitle{font-size:1.8rem}.sectionSubtitle{display:block;font-size:1.1rem;margin-bottom:2rem}.sectionTitle,.sectionSubtitle{text-align:center}.sectionTitleDark,.sectionSubtitleDark{color:#fdf5ff}.container{max-width:95%;margin:0 auto}.grid{display:grid;gap:1.5rem}.scrollup{position:fixed;right:1rem;bottom:-20rem;background-color:#9932cc;color:#fdf5ff;opacity:0.8;padding:0 0.3rem;border-radius:0.4rem;z-index:10;transition:0.5s}.scrollup:hover{background-color:#c042ff}.scrollup.active{bottom:4rem;transition:0.5s}::-webkit-scrollbar{width:0.6rem;background-color:#f4e2fd;border-radius:0.5rem}::-webkit-scrollbar-thumb{background-color:#9932cc;border-radius:0.5rem}::-webkit-scrollbar-thumb:hover{background-color:#311042}@media screen and (max-width: 350px){.container{max-width:90%}}@media screen and (min-width: 768px){.container{max-width:80%}body{margin:0}.section{padding:6rem 0 2rem}.sectionSubtitle{margin-bottom:4rem}}@media screen and (min-width: 1024px){.container{max-width:70%}}.contactContainer{row-gap:3rem}.contactInfo{display:flex;margin-bottom:2rem}.phoneIcon{color:#9932cc;margin-right:.75rem}.contactTitle{font-size:1.3rem;font-weight:500}.contactDesc{font-size:1.1rem;color:#999}.contactForm .contactContent{background-color:#eee;border-radius:0.5rem;padding:0.75rem 1rem 0.25rem}.contactForm .contactContentDark{background-color:#2b0e3a;border-radius:0.5rem;padding:0.75rem 1rem 0.25rem}.contactForm .contactLabel{font-size:1rem}.contactForm input,.contactForm textarea{width:100%;background-color:inherit;color:#747474;font-size:1.2rem;border:none;outline:none;padding:0.5rem 0}.contactForm button{display:flex;align-items:center;justify-content:center;padding:8px 10px;width:160px;height:40px;background-color:#9932cc;color:white;border-radius:8px;box-shadow:0 4px 4px rgba(0,0,0,0.2);transition:1s;border:none;cursor:pointer}.contactForm button .messageIcon{margin-left:4px}.contactForm button:hover{background-color:#5a0086;box-shadow:0 5px 8px rgba(0,0,0,0.2)}.contactForm button:focus{outline:none}.contactForm button:hover>.messageIcon{transform:translateX(0.5rem)}@media screen and (min-width: 560px){.contactContainer{grid-template-columns:repeat(2, 1fr);row-gap:1rem}}@media screen and (min-width: 1024px){.contactContainer{grid-template-columns:1fr 1.5fr;row-gap:1rem}.contactInputs{grid-template-columns:repeat(2, 1fr)}}
