*{margin:0;padding:0;box-sizing:border-box}html{scroll-behavior:smooth}.loader{text-align:center;display:flex;justify-self:center;align-self:center;display:block;margin:20rem auto}body{margin:0 0 3rem 0;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:1.2rem}.lightTheme{background-color:#fdf5ff;color:#747474}.darkTheme{background-color:#311042;color:#f4e2fd}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}h1,h2,h3,h4{color:#4d4d4d;font-weight:700}ul{list-style:none}a{text-decoration:none}img{max-width:100%;height:auto}.section{padding:2rem 0 3rem}.sectionTitle{font-size:1.8rem}.sectionSubtitle{display:block;font-size:1.1rem;margin-bottom:2rem}.sectionTitle,.sectionSubtitle{text-align:center}.sectionTitleDark,.sectionSubtitleDark{color:#fdf5ff}.container{max-width:95%;margin:0 auto}.grid{display:grid;gap:1.5rem}.scrollup{position:fixed;right:1rem;bottom:-20rem;background-color:#9932cc;color:#fdf5ff;opacity:0.8;padding:0 0.3rem;border-radius:0.4rem;z-index:10;transition:0.5s}.scrollup:hover{background-color:#c042ff}.scrollup.active{bottom:4rem;transition:0.5s}::-webkit-scrollbar{width:0.6rem;background-color:#f4e2fd;border-radius:0.5rem}::-webkit-scrollbar-thumb{background-color:#9932cc;border-radius:0.5rem}::-webkit-scrollbar-thumb:hover{background-color:#311042}@media screen and (max-width: 350px){.container{max-width:90%}}@media screen and (min-width: 768px){.container{max-width:80%}body{margin:0}.section{padding:6rem 0 2rem}.sectionSubtitle{margin-bottom:4rem}}@media screen and (min-width: 1024px){.container{max-width:70%}}.header{width:100%;position:fixed;bottom:0;left:0;z-index:100;background-color:#fdf5ff}.headerDark{width:100%;position:fixed;bottom:0;left:0;z-index:100;background-color:#9932cc}.header.scroll{box-shadow:0 -1px 4px rgba(0,0,0,0.15)}.nav{max-width:968px;height:3rem;display:flex;justify-content:space-between;align-items:center}.nav__logo,.navToggle{color:#4d4d4d;font-weight:500;font-size:1.8rem;color:#9932cc}.navToggle{font-size:1.1rem;cursor:pointer;display:flex;align-items:center}.navToggleIcon:hover,.changeTheme:hover{color:#9932cc}.changeTheme{margin-right:1rem}@media screen and (max-width: 767px){.navMenu{position:fixed;bottom:-100%;left:0;width:100%;background-color:#fdf5ff;padding:2rem 1.5rem 4rem;box-shadow:0 -1px 4px rgba(0,0,0,0.15);border-radius:1.5rem 1.5rem 0 0;transition:0.5s;z-index:20}.show__menu{bottom:0;z-index:1000}}.navList{display:grid;grid-template-columns:repeat(3, 1fr);gap:2rem}.navLink{display:flex;flex-direction:column;align-items:center;font-size:1.1rem;color:#4d4d4d;font-weight:500}.navLink:hover{color:#9932cc}.navIcon{font-size:1.2rem;margin-bottom:5px}.navClose{position:absolute;right:1.3rem;bottom:0.5rem;font-size:1.5rem;cursor:pointer;color:#9932cc}.navClose:hover{color:#c042ff}@media screen and (min-width: 768px){.header{top:0;bottom:initial;padding:0 1rem}.nav{height:4.5rem;column-gap:1rem}.navIcon,.navToggleIcon,.navClose{display:none}.changeTheme{color:#4d4d4d;margin:0}.navList{display:flex;column-gap:2rem}.navMenu{margin-left:auto}}@media screen and (min-width: 1024px){.header{padding:0}}
