*{margin:0;padding:0;box-sizing:border-box}html{scroll-behavior:smooth}.loader{text-align:center;display:flex;justify-self:center;align-self:center;display:block;margin:20rem auto}body{margin:0 0 3rem 0;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:1.2rem}.lightTheme{background-color:#fdf5ff;color:#747474}.darkTheme{background-color:#311042;color:#f4e2fd}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}h1,h2,h3,h4{color:#4d4d4d;font-weight:700}ul{list-style:none}a{text-decoration:none}img{max-width:100%;height:auto}.section{padding:2rem 0 3rem}.sectionTitle{font-size:1.8rem}.sectionSubtitle{display:block;font-size:1.1rem;margin-bottom:2rem}.sectionTitle,.sectionSubtitle{text-align:center}.sectionTitleDark,.sectionSubtitleDark{color:#fdf5ff}.container{max-width:95%;margin:0 auto}.grid{display:grid;gap:1.5rem}.scrollup{position:fixed;right:1rem;bottom:-20rem;background-color:#9932cc;color:#fdf5ff;opacity:0.8;padding:0 0.3rem;border-radius:0.4rem;z-index:10;transition:0.5s}.scrollup:hover{background-color:#c042ff}.scrollup.active{bottom:4rem;transition:0.5s}::-webkit-scrollbar{width:0.6rem;background-color:#f4e2fd;border-radius:0.5rem}::-webkit-scrollbar-thumb{background-color:#9932cc;border-radius:0.5rem}::-webkit-scrollbar-thumb:hover{background-color:#311042}@media screen and (max-width: 350px){.container{max-width:90%}}@media screen and (min-width: 768px){.container{max-width:80%}body{margin:0}.section{padding:6rem 0 2rem}.sectionSubtitle{margin-bottom:4rem}}@media screen and (min-width: 1024px){.container{max-width:70%}}.skillsContainer{row-gap:0}.skillsHeader{display:flex;align-items:center;margin-bottom:.75rem;cursor:pointer}.skillsIcon,.skillsArrow{font-size:2rem;color:#9932cc}.skillsIcon{margin-right:.75rem}.skillsData{row-gap:3.5rem;padding-left:2.7rem;margin-bottom:0.6rem}.skillsClose .skillsData{height:0;overflow:hidden;transition:1s}.skillsOpen .skillsData{height:max-content;margin-bottom:1rem;transition:1s}.skillsOpen .skillsArrow{transform:rotate(-180deg)}.skillsListGrid{display:flex;flex-wrap:wrap}.skillsTitle{font-size:1.3rem;margin-bottom:.5rem}.skillsTitleDark{color:#fdf5ff}.skillsArrow{margin-left:auto;transition:0.5s}.skillsSubtitle{font-size:1.1rem;color:#999}.skillName{font-size:1.2rem;font-weight:500;margin-bottom:.75rem}.skillNameDark{color:#fdf5ff}.skillBar,.skillPerc{height:5px;border-radius:0.25rem}.skillBar{background-color:#c2c2c2}.skillPerc{display:block;background-color:#9932cc}.skillHtml{width:85%}.skillJavascript{width:69%}.skillReact{width:73%}.skillGatsby{width:43%}.skillNode{width:79%}.skillFirebase{width:73%}.skillMongo{width:73%}.skillMysql{width:57%}.skillPostgres{width:49%}.skillsList{display:flex;flex-wrap:wrap}.skillsList span{margin:4px;padding:8px 10px;background-color:#9932cc;color:white;border-radius:8px;transition:1s}@media screen and (min-width: 560px){.skillsContainer{grid-template-columns:repeat(2, 1fr)}}@media screen and (min-width: 1250px){.skillsContainer{grid-template-columns:repeat(3, 1fr)}}
